@import url("https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
}

body {
  font-family: "Tenor Sans", sans-serif;
  font-weight: 400;
}

.btn {
  font-family: "Tenor Sans", sans-serif;
  background-color: #f3d5ae;
  border-radius: 4px;
  padding: 14px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}
.btn:hover {
  background-color: #f6c483;
}

.h2 {
  font-size: 32px;
  line-height: 130%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #262626;
}

.header {
  background-color: #7f713e;
}
.header__nav {
  display: flex;
  align-items: center;
  padding: 16px 0px;
}
@media (max-width: 1100px) {
  .header__nav {
    justify-content: space-between;
    height: 60px;
  }
}
.header__logo {
  max-width: 143px;
}
@media (max-width: 1100px) {
  .header__logo {
    display: none;
  }
}
.header__logo-mobile {
  max-width: 33px;
  display: none;
}
@media (max-width: 1100px) {
  .header__logo-mobile {
    display: block;
  }
}
.header__logo-img {
  max-width: 100%;
}
.header__menu {
  display: flex;
  gap: 8px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .header__menu {
    display: none;
  }
}
.header__menu-link, .header__phone {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.header__menu-link {
  padding: 6px 10px;
  transition: 0.2s;
}
.header__menu-link:hover {
  border: 1px solid #fff;
  border-radius: 100px;
}
.header__phone:hover {
  border-bottom: 1px solid #fff;
}
.header__social {
  display: flex;
  gap: 16px;
  margin-left: 32px;
}
@media (max-width: 1100px) {
  .header__social {
    display: none;
  }
}
.header__social-link {
  transition: 0.2s;
}
.header__social-link:hover {
  transform: translateY(-3px);
}
.header__burger {
  display: none;
  width: 24px;
  height: 21px;
  background-image: url(../img/burger.svg);
  background-repeat: no-repeat;
  transition: 0.2s;
}
@media (max-width: 1100px) {
  .header__burger {
    display: block;
  }
}
.header__burger.close {
  background-image: url(../img/burger-close.svg);
}

.header-main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.4s;
}

@media (max-width: 1100px) {
  .header-main.show {
    background-color: #7f713e;
  }
}

.mobile-menu {
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  padding-bottom: 16px;
  transform: translateX(120%);
  background-color: #7f713e;
  transition: 0.4s;
}
.mobile-menu__item {
  text-align: center;
  margin-top: 16px;
}
.mobile-menu__link {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.mobile-menu__social {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}
.mobile-menu.show {
  transform: translateX(0%);
}

.intro {
  padding: 248px 0;
  background-image: url(../img/intro-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 480px) {
  .intro {
    background-image: url(../img/intro-bg-mobile.jpg);
    padding: 245px 0;
  }
}
@media (max-width: 500px) {
  .intro__inner {
    max-width: 324px;
    margin: 0 auto;
  }
}
.intro__title {
  font-size: 48px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 16px;
}
@media (max-width: 600px) {
  .intro__title {
    font-size: 32px;
  }
}
.intro__text {
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  color: #fff;
  margin: 0 auto;
  margin-bottom: 40px;
  max-width: 477px;
}
@media (max-width: 600px) {
  .intro__text {
    font-size: 16px;
  }
}
@media (max-width: 350px) {
  .intro__text {
    font-size: 14px;
  }
}
.intro__btn {
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: #513f29;
  margin: 0 auto;
}

.about {
  margin-top: 120px;
}
@media (max-width: 500px) {
  .about {
    margin-top: 64px;
  }
}
.about__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
}
@media (max-width: 950px) {
  .about__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.about__col--1 {
  max-width: 560px;
}
@media (max-width: 950px) {
  .about__col--1 {
    order: 1;
  }
}
.about__img {
  max-width: 100%;
}
.about__col--2 {
  max-width: 460px;
}
@media (max-width: 950px) {
  .about__col--2 {
    max-width: 560px;
  }
}
.about__title {
  margin-bottom: 42px;
}
@media (max-width: 500px) {
  .about__title {
    font-size: 24px;
    margin-bottom: 23px;
  }
}
.about__text {
  font-size: 24px;
  line-height: 150%;
  color: #262626;
  margin-top: 33px;
}
@media (max-width: 500px) {
  .about__text {
    font-size: 16px;
    margin-top: 23px;
  }
}

.catalog__header {
  display: flex;
  justify-content: space-between;
}
.catalog__more {
  display: flex;
  gap: 8px;
  align-items: center;
}
.catalog__more-text {
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #7f713e;
}
.catalog__gallery {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 300px);
  grid-auto-rows: 300px;
}